body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navigation{position:fixed;top:0;left:0;bottom:0;padding:0px;display:flex;flex-flow:row wrap;align-items:center;justify-content:center;width:14%;background-color:#2a4765;z-index:10000}.navigation .logo{display:flex;flex-flow:column}.navigation .logo .profileCirkle{border-radius:50%;width:120px;height:120px;background-color:gray}.navigation .logo .profileCirkle .profileImage{width:70px}.navigation .logo .text{color:#fbfbfc;display:flex;flex-flow:column;justify-content:center;align-items:flex-start}.navigation .logo .text h4{margin:2px}.navigation .menu a{display:inline-block;padding:0 30px;font-size:1rem;color:#fbfbfc}.navigation .menu a:hover{color:tomato}.navigation .menu .active{color:tomato;font-weight:bold}

.grid-container{display:grid;grid-template-columns:20% 20% 20% 10%;grid-gap:10px;background-color:white;padding:10px;margin:0 14%;height:auto}.grid-container>div{padding:0px}.grid-container .item1{grid-column:1 / 5}.grid-container .item2{grid-column:5/5}

.newsfeed{background-color:white;text-align:left;border-style:solid;border-width:1px}.newsfeed h1{text-align:center;margin:4%}

.newsbox{display:grid;grid-template-columns:10% 20% 20% 40% 10%;grid-template-rows:40% 60%;text-align:left;margin:10px;border-style:solid;border-width:1px;background-color:#fafafa;align-items:left}.newsbox img{width:50px;height:50px;border-radius:50%;margin:15px;object-fit:cover}.newsbox .profileimage{grid-column:1}.newsbox .name{grid-column:2/ 5;grid-row:span 1;margin-top:4%;margin-left:4%}.newsbox .message{grid-column:2 / 5;grid-row:2;margin-top:0px;margin-bottom:3%;margin-left:4%}.newsbox h2{color:#4b4a4a;float:left;font-size:1rem;margin:0px}

.info{border-style:solid;background-color:white;border-width:1px}.info h1{text-align:center;margin:4%}

.notisbox{display:grid;grid-template-columns:5% 10% 70%;text-align:left;margin:10px;border-style:solid;border-width:1px;background-color:#fafafa}.notisbox .notisicon{grid-column:2;grid-row:1;margin-top:30%;align-items:center}.notisbox .notismsg{grid-column:3;grid-row:1}

.onGoing{margin:0 14%;padding:30px}.onGoing h1{text-align:left;margin:30px 0}.onGoing .coursBoxes{display:flex;flex-flow:row wrap;justify-content:space-between}

.courseBox{position:relative;width:300px;height:320px;border-radius:10px;margin:10px 0;background-color:#e5e3e3;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19)}.courseBox .box{margin:0;padding:0;height:100%;width:100%}.courseBox:hover .box{transition:0.4s ease-out;-webkit-filter:brightness(50%);filter:brightness(50%);background-color:rgba(128,128,128,0.603);border-radius:10px}.courseBox:hover .open{transition:0.2s ease-out;position:absolute;top:35%;justify-content:center;opacity:1;right:0;width:100%}.courseBox:hover .open h2{font-size:1.5rem;color:white;text-align:center;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19)}.courseBox .open{opacity:0}.courseBox .imageBox{width:100%;height:50%;border-radius:10px 10px 0 0;background-color:white}.courseBox .imageBox img{justify-content:center;width:100%;height:100%;border-radius:10px 10px 0 0;object-fit:cover}.courseBox .newCourseBox{display:flex;justify-content:center;background-color:#0f9814;color:white;height:25px;width:60px;margin:-13% 0 0 4%;position:relative;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19)}.courseBox .newCourseBox h4{font-size:16px}.courseBox .hp{display:flex;color:#1f1f1f;float:right;align-items:center;margin:10px}.courseBox .hp .graduationCap{margin:0 5px}.courseBox h2{color:#1f1f1f;float:left;text-align:left;width:66%;font-size:1rem;margin:10px 0 0 10px}.courseBox p{color:#707070;margin:0 10px 0 10px;font-size:0.8rem;float:left;text-align:left}.courseBox .participants{position:absolute;bottom:0px;display:flex;color:#1f1f1f;float:left;align-items:center;margin:10px}.courseBox .participants .peopleicon{margin:5px}.courseBox .progressBar{position:absolute;right:0px;bottom:0px;width:40px;margin:10px}



.assignments{margin:0px 200px}.assignments h1{float:left;margin-top:-35px;margin-left:-10px;font-size:30px;color:#1b1b1e}.assignments .assignmentboxes{display:flex;flex-flow:row wrap;justify-content:space-between}.quizes{margin:100px 200px}.quizes h1{float:left;margin-top:-40px;margin-left:-10px;font-size:30px;color:#1b1b1e}.quizes .quizboxes{display:flex;flex-flow:row wrap;justify-content:space-between}

.assignment h4{color:#1b1b1e;font-weight:1000;margin:10px 0 0 10px}.assignment .assignmentBox{width:250px;height:180px;margin:30px 0 10px;background-color:#e5e3e3;border-radius:10px 10px 10px 10px;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19)}.assignment .assignmentBox .passed h2{float:left;margin:30px 82px 20px;color:#73A790}.assignment .assignmentBox .notPassed h2{float:left;margin:30px 60px 20px;color:#D7B17C}.assignment .assignmentBox .notSubmitted{background-color:#e5e3e3}.assignment .assignmentBox .notSubmitted h2{float:left;margin:30px 42px 20px;color:#707070}.assignment .assignmentBox .submitted h2{float:left;margin:30px 64px 20px;color:#707070}.assignment .assignmentBox .assigInfo{margin:0px 30px 0px;color:#707070}.assignment .assignmentBox .assigInfo .duration{margin-top:20px}.assignment .assignmentBox .assigInfo .duration p{font-weight:100}

.quiz h4{color:#1b1b1e;font-weight:1000;margin:10px 0 0 10px}.quiz .quizBox{width:250px;height:180px;margin:30px 0 10px;background-color:#e5e3e3;border-radius:10px 10px 10px 10px;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19)}.quiz .quizBox .submitted h2{float:left;margin:30px 70px 20px;color:#707070}.quiz .quizBox .notSubmitted h2{float:left;margin:30px 60px 20px;color:#707070}.quiz .quizBox .quizInfo{margin:0px 30px 0px;color:#707070}.quiz .quizBox .progress{margin-top:50px}.quiz .quizBox .progress p{font-weight:100}

.bar{position:fixed;top:0;right:0;bottom:0;background-color:#2a4765;width:14%;z-index:10000}

.app{text-align:center}

html{scroll-behavior:smooth}body{margin:0;padding:0;font-family:"Montserrat", sans-serif;background:#fbfbfc;background-size:100% auto;background-repeat:repeat-y}section{min-height:calc(100vh - 160px);padding:80px}h1{color:#1b1b1e;font-family:"Montserrat", sans-serif;font-weight:700;margin:0}h2,h3{font-family:"Montserrat", sans-serif;font-weight:700;margin:0;color:#fbfbfc}h4,h5,h6{font-family:"Montserrat", sans-serif;font-weight:500;margin:0}p{font-weight:500;margin:0;color:#707070;font-size:0.8rem}a{font-family:"Montserrat", sans-serif;font-weight:400;text-decoration:none;margin:0}

.coursePage{margin:30px 14%}.coursePage .courseName{padding:30px 30px;text-align:left}.coursePage .coursePage-container{display:flex;flex-flow:row;justify-content:space-evenly}.coursePage .coursePage-container .course-info{margin:0}.coursePage .coursePage-container .right{display:flex;flex-flow:column}.coursePage .coursePage-container .right .calendar-box{height:300px;background-color:#e5e3e3;border-radius:10px;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19);margin:0 0 40px 0}.coursePage .coursePage-container .right .news{height:300px;border-radius:10px;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19);background-color:#e5e3e3}.coursePage .coursePage-container .right .news h2{float:left;margin:20px;text-align:left;color:#1b1b1e;font-size:13}

.courseInfo{position:relative;display:flex;flex-flow:column;background-color:#e5e3e3;height:600px;width:600px;padding:20px;border-radius:10px;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19)}.courseInfo .profiles{display:flex;flex-flow:row}.courseInfo h2{float:left;text-align:left;color:#1b1b1e;font-size:13}.courseInfo p{text-align:left;float:left}.courseInfo .literature a{color:inherit;margin:3px}.courseInfo .literature a .book1{width:60px;height:70px;margin:20px 0}.courseInfo .literature a .book1 img{width:60px;height:90px;object-fit:cover}.courseInfo .software{display:flex;flex-flow:column}.courseInfo .software .program1{margin:20px 0}.courseInfo .software .program1 .logo{float:left;height:60px;width:60px}.courseInfo .participants{position:absolute;bottom:0px;display:flex;color:#1f1f1f;float:left;align-items:center;margin:20px 0}.courseInfo .participants .peopleicon{margin:5px}.courseInfo .progressBar{position:absolute;right:0px;bottom:0px;width:80px;margin:20px}

.profileBox{width:100px;margin:10px;display:flex;flex-flow:column;align-items:center}.profileBox h4{font-size:0.8rem}.profileBox h5{color:#707070;font-size:0.7rem}.profileBox .profileImage{width:60px;height:60px;object-fit:cover;border-radius:50%}.profileBox .quickLinks a{color:inherit;margin:3px}

.calendar{margin:20px;width:250px}

